h1 {
  color: aliceblue;
  line-height: 1.5;
  font-size: 4em;
  text-align: left;
  margin-left: 3.5%;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
}

@media screen and (max-width:768px) {
  h1 {
    font-size: 2.5em;
  }
  .submitButton{
    margin: 8% 0;
    width: 80%;
  }


}
input {
  text-align: left;
  margin-right: 1.5%;
  margin-left: 0.5%;
  border: none;
  border-bottom: 2.5px solid white;
  background-color: inherit;
  width: 350px;
  color: white;
  font-family: "Ubuntu";
  font-size: 2rem;
}

.my-form {
  text-align: left;
  margin-top: 2%;
  padding-left: 3.5%;
}
body {
  padding: 5px 20px 20px 20px;
  background-color: #0a1f44;
}
.firstPage {
  text-align: center;
}

.submitButton {
  margin-left: 20px;
}
.error-message {
  color: red;
  text-align: left;
  margin-left: 3.5%;
}

.country {
  font-size: 0.5em;
  padding: 0.2em 0.6em;
  border-radius: 30px;
  color: white;
  background: orange;
}
.weather-container {
  display: flex;
  flex-wrap: wrap;
  flex: 1 1 300px;
}
.weather-item {
  padding: 2%;
  flex: 0.3;
  width: fit-content;
  justify-content: flex-start;
}

.card {
  border-radius: 20px;
  width: 100%;
  height: 50%;
  padding: 5%;
  min-width: 300px;
}
.container h2 {
  color: #53627c;
  font-size: 1.5em;
  text-align: left;
}

.city-temp {
  color: #1e2432;
  font-size: 5.5rem;
  padding-top: 0.1em;
  text-align: left;
  font-weight: 600;
}

.city-temp sup {
  font-size: 0.6em;
}

.container figure {
  text-align: left;
  color: #53627c;
}

.Footer {
  color: aliceblue;
  font-family: "Ubuntu";
  margin-bottom: 22px;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
}
